@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.util-flex {
	display: -webkit-flex;
	display: flex;
}

.util-flex-1 {
	-webkit-flex: 2 1;
	        flex: 2 1;
}

.util-center {
	-webkit-align-items: center;
	        align-items: center;
}

.util-space-between {
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.util-flex-column {
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.card {
	position: relative;
	width: 98%;
	background-color: white;
	margin: 10px auto;
	border-radius: 5px;
	box-shadow: 1px 1px 10px rgb(212, 212, 212), -1px -1px 10px rgb(212, 212, 212);
}

.padding-10 {
	padding: 10px;
}

.margin-side-10 {
	margin: 10px;
}

.product-item {
	display: inline-block;
	width: 15%;
	margin: 20px 10px;
	text-align: center;
	cursor: pointer;
}

.link {
	text-decoration: none;
	color: black;
}

.link:hover {
	color: var(--primary);
}

.product-name {
	margin: 15px 5px 5px 5px;
	font-size: 15px;
}

.product-num-of-ratings {
	color: var(--secondary);
}

.product-price {
	margin: 5px;
	/* font-weight: 300; */
	color: var(--secondary);
}

.product-ratings {
	position: relative;
	font-size: 14px;

	background-color: var(--green);
	color: var(--pure);
	padding: 0.1rem 0.5rem;
	border-radius: 0.2rem;
}

.faStar {
	height: 9px;
	-webkit-transform: translate(0, -25%);
	        transform: translate(0, -25%);
	margin-left: 2px;
}

.product-num-of-ratings {
	margin-left: 8px;
}

.card-title {
	font-size: 22px;
	line-height: 32px;
	font-weight: 500;
	margin-top: 10px;
	margin-left: 10px;
}

.card-sub-title {
	font-size: 16px;
	color: #c2c2c2;
	font-weight: 400;
	margin-left: 10px;
}

.view-all {
	position: absolute;
	top: 20px;
	right: 20px;
	background-color: var(--primary);
	color: white;
	padding: 10px 20px;
	border: none;
	border-radius: 3px;
	font-size: 14px;
	font-weight: bold;
	font-family: Roboto, Arial, sans-serif;
	cursor: pointer;
}

.hr {
	border-color: rgba(255, 255, 255, 0.4);
	margin-top: 20px;
}


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: "Roboto", Arial, Helvetica, sans-serif;
	line-height: 1.4;
}

:root {
	--primary: #2874f0;
	--secondary: #878787;
	--green: #388e3c;
	--yellow: #ff9f00;
	--orange: #fb641b;
	--pure: #fff;
	--background: #eaeded;
}

body {
	background-color: #eaeded;
	background-color: var(--background);
}

.red {
	background-color: red;
}
.newone {
	background-color: green;
}

.btn-primary {
	display: inline-block;
	width: 200px;
	padding: 1.2rem 2.51rem;
	border: none;
	color: var(--pure);
	font-size: 0.9rem;
	margin: 0.2rem;
	font-weight: bold;
	border-radius: 0.2rem;
	text-decoration: none;
    text-align: center;
}

.yellow-btn {
	background-color: var(--yellow);
}

.orange-btn {
	background-color: var(--orange);
}

.width-auto {
    width: auto;
}

.left__product-image {
	margin: 5rem 5rem 2rem 5rem;
}

.btn-container {
	margin-top: 1.5rem;
}

.left-container {
	width: -webkit-max-content;
	width: max-content;
	text-align: center;
}

.icon {
	margin: 0 0.4rem 0px 0;
}

.right-container {
	margin: 0 2rem;
}

.right__product-name {
	font-size: 1.5rem;
}

.right__product-price {
	font-size: 1.5rem;
	font-weight: 500;
	margin: 1rem 0;
}

.right__label-product {
	color: var(--secondary);
	font-weight: 500;
	margin: 1rem 0;
}

.right__product-line-height {
	line-height: 1.7rem;
}

.right__product-highlights ul li {
	color: #3d3d3d;
}

.right__product-details {
	color: #3d3d3d;
}

.right {
	background-color: red;
}
.container {
	width: 85%;
	height: 100%;
	background-color: var(--pure);
	margin: auto;
	padding: 2rem;
}
.input-field {
	width: 60%;
	height: 30px;
	padding: 18px;
	font-size: 16px;
	border: none;
	box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.5);

	font-family: fontawesome, Arial, Helvetica, sans-serif;
}

.icon-search {
	background-color: var(--pure);
	color: var(--primary);
	height: 100%;
	width: 100%;
	padding: 5px 10px 5px 5px;
}

.nav-bar-container {
	background-color: var(--primary);
	color: var(--pure);
	padding: 0.7rem;
	position: -webkit-sticky;
	position: sticky;
}

.nav-bar {
	width: 75%;
	margin: auto;
}

.logo {
	margin-right: 1rem;
	text-align: center;
	font-size: 1.4rem;
	font-weight: bold;
	color: var(--pure);
	text-decoration: none;
	margin: auto 1rem;
}

.cart {
	margin: auto;
}

.icon-cart {
	margin-right: 0.5rem;
}

.buy-now-page__title {
	background-color: var(--primary);
	color: var(--pure);
	padding: 0.7rem 1.5rem;
}

.buy-now-page__container {
	padding: 1rem 2rem;
	line-height: 3rem;
}

.buy-now-page__img-container {
	margin: auto 0;
}

.buy-now-page__order-details-container {
	margin: auto 2rem;
}

.buy-now-page__order-details-container > div {
	margin: 1rem;
}

.buy-now-page__product-name {
	font-size: 1.2rem;
	font-weight: 300;
}

.buy-now-page__product-price {
	font-size: 1.5rem;
	font-weight: 500;
}

.btn {
	position: absolute;
	right: 0;
	margin-right: 10px;
}

.back-to-home-btn {
    position: absolute;
    right: 0;
    margin-right: 10px;
}
